body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica";
  background: -webkit-linear-gradient(1deg, #a4b357 0%, #13bd04 100%);
  background: linear-gradient(89deg, #a4b357 0%, #13bd04 100%);
}

button {
	cursor: pointer;
}
.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
}
.Tilt {
	background: -webkit-linear-gradient(1deg, #13bd04 0%, #a4b357 100%);
	background: linear-gradient(89deg, #13bd04 0%, #a4b357 100%);
}
.form {
	width: 700px;
	background: -webkit-radial-gradient(0% 50%, 
	circle, 
	rgba(96, 16, 48, 0) 9px, #613 10px, 
	rgba(96, 16, 48, 0) 11px) 0px 10px,
	-webkit-radial-gradient(at 100% 100%,      
	rgba(96, 16, 48, 0) 9px, #613 10px, rgba(96, 16, 48, 0) 11px),
	#8a3;
	background: radial-gradient(circle at 0% 50%, 
	rgba(96, 16, 48, 0) 9px, #613 10px, 
	rgba(96, 16, 48, 0) 11px) 0px 10px,
	radial-gradient(at 100% 100%,      
	rgba(96, 16, 48, 0) 9px, #613 10px, rgba(96, 16, 48, 0) 11px),
	#8a3;
	background-size: 20px 20px;
}
.App {
  text-align: center;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
